// src/pages/TestPage.jsx
import React, { useEffect, useState } from 'react';
import { testConnection } from '../utils/testAirtable';

const TestPage = () => {
    const [status, setStatus] = useState('Testing...');

    useEffect(() => {
        const test = async () => {
            const result = await testConnection();
            setStatus(result ? 'Connected!' : 'Failed to connect');
        };
        test();
    }, []);

    return (
        <div className="p-8">
            <h1 className="text-2xl mb-4">Airtable Connection Test</h1>
            <p>Status: {status}</p>
        </div>
    );
};

export default TestPage;