// src/components/withSiteContent.jsx
import React from 'react';
import { getSiteContent } from '../utils/sheetsFetcher';

// Higher-order component to provide content to pages
export const withSiteContent = (WrappedComponent) => {
    return function WithSiteContentComponent(props) {
        const siteContent = getSiteContent()?.content;

        if (!siteContent) {
            return (
                <div className="min-h-screen flex items-center justify-center">
                    <p className="text-gray-600">Please publish site content from the admin panel</p>
                </div>
            );
        }

        return <WrappedComponent {...props} content={siteContent} />;
    };
};

// Usage example for Members page
const MembersPage = ({ content }) => {
    const { members } = content;

    return (
        <div>
            {members.map(member => (
                <div key={member.Name}>
                    <h2>{member.Name}</h2>
                    <p>{member.Bio}</p>
                    {member.ImageUrl && <img src={member.ImageUrl} alt={member.Name} />}
                </div>
            ))}
        </div>
    );
};

export default withSiteContent(MembersPage);