import React, { useState } from 'react';
import { Trophy, Award, Star, Calendar, ChevronDown, Medal, Target, Users } from 'lucide-react';

const YearSelector = ({ years, selectedYear, onYearChange }) => (
    <div className="flex gap-4 overflow-x-auto pb-4">
        {years.map((year) => (
            <button
                key={year}
                onClick={() => onYearChange(year)}
                className={`px-6 py-3 rounded-lg font-medium transition-all
                    ${selectedYear === year
                        ? 'bg-emerald-500 text-white shadow-lg shadow-emerald-200'
                        : 'bg-white text-gray-600 hover:bg-emerald-50'}`}
            >
                {year}
            </button>
        ))}
    </div>
);

const AchievementCard = ({ achievement }) => {
    const [isExpanded, setIsExpanded] = useState(false);

    return (
        <div className="bg-white rounded-xl shadow-lg overflow-hidden group">
            <div className="p-6 space-y-4">
                <div className="flex items-start gap-4">
                    <div className="w-12 h-12 rounded-lg bg-emerald-50 flex items-center justify-center
                                flex-shrink-0 group-hover:bg-emerald-100 transition-colors">
                        {React.createElement(achievement.icon, {
                            className: "w-6 h-6 text-emerald-600"
                        })}
                    </div>
                    <div className="space-y-2 flex-grow">
                        <h3 className="text-xl font-semibold text-gray-800 group-hover:text-emerald-600 
                                   transition-colors">
                            {achievement.title}
                        </h3>
                        <div className="flex items-center gap-4 text-sm text-gray-500">
                            <div className="flex items-center gap-1">
                                <Calendar className="w-4 h-4" />
                                <span>{achievement.date}</span>
                            </div>
                            {achievement.participants && (
                                <div className="flex items-center gap-1">
                                    <Users className="w-4 h-4" />
                                    <span>{achievement.participants} members</span>
                                </div>
                            )}
                        </div>
                    </div>
                </div>

                <p className={`text-gray-600 transition-all duration-300
                            ${isExpanded ? '' : 'line-clamp-2'}`}>
                    {achievement.description}
                </p>

                {achievement.impact && (
                    <div className="pt-4 border-t border-gray-100">
                        <div className="grid grid-cols-2 md:grid-cols-3 gap-4">
                            {achievement.impact.map((stat, index) => (
                                <div key={index} className="text-center">
                                    <div className="text-2xl font-bold text-emerald-600">
                                        {stat.value}
                                    </div>
                                    <div className="text-sm text-gray-600">
                                        {stat.label}
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                )}

                <button
                    onClick={() => setIsExpanded(!isExpanded)}
                    className="flex items-center gap-1 text-emerald-600 hover:text-emerald-700 
                             transition-colors text-sm font-medium"
                >
                    {isExpanded ? 'Show less' : 'Show more'}
                    <ChevronDown className={`w-4 h-4 transition-transform duration-300 
                                         ${isExpanded ? 'rotate-180' : ''}`} />
                </button>
            </div>
        </div>
    );
};

