// src/utils/testAirtable.js
import Airtable from 'airtable';

const base = new Airtable({
    apiKey: import.meta.env.VITE_AIRTABLE_PAT
}).base('appC9uTNAHaavpGmk');

// Simple test function
export const testConnection = async () => {
    console.log('Testing with token:', import.meta.env.VITE_AIRTABLE_PAT);
    console.log('Testing with base:', 'appC9uTNAHaavpGmk');

    try {
        const records = await base('Members').select({
            maxRecords: 1
        }).firstPage();

        console.log('Success! First record:', records[0]?.fields);
        return true;
    } catch (error) {
        console.error('Connection failed:', {
            message: error.message,
            statusCode: error.statusCode,
            details: error
        });
        return false;
    }
};